body {
  font-family: Courier New, Courier, monospace;
  font-size: 16px;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

.container {
  max-width: 90%;
  margin: 0 auto;
}

.visually-hidden {
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  border-bottom: 5px solid #0ff;
  max-width: 100%;
  margin: 0 auto;
  padding-bottom: 10px;
}

form {
  width: 300px;
  margin: 0 auto;
  position: relative;
}

input, button {
  border: none;
  border-radius: 3px;
  outline: none;
}

input {
  color: #fff;
  background: #3df0fe;
  width: 100%;
  height: 50px;
  padding-left: 15px;
  font-size: large;
}

button {
  cursor: pointer;
  background: #01d5e6;
  width: 36px;
  height: 36px;
  transition: background .5s ease-in-out;
  position: absolute;
  top: 8px;
  right: -10px;
}

button:before {
  content: "➡";
  color: #fff;
  font-size: 26px;
  font-weight: bold;
  transition: color .5s ease-in-out;
  position: absolute;
  top: 0;
  left: 5px;
}

button:hover {
  background: #90f6fe;
  box-shadow: 2px 3px 10px #90f6fe;
}

button:hover:before {
  color: #01a2af;
}

.gallery {
  flex-wrap: wrap;
  gap: 24px;
  padding: 12px;
  display: flex;
}

.photo-card {
  outline: 1px solid teal;
  width: calc(33.3333% - 16px);
}

.info {
  padding: 0 26px;
}

